/* --------FORM in modal window------------- */
  const formModalRef = document.querySelector('[data-form-modal]');

  const takeFormData = event => {
    event.preventDefault();//–∑–∞–±–æ—Ä–æ–Ω—è—î –±—Ä–∞—É–∑–µ—Ä—É –≤—ñ–¥–ø—Ä–∞–≤–ª—è—Ç–∏ —Ñ–æ—Ä–º—É –ø—Ä–∏ –Ω–∞—Ç–∏—Å–∫–∞–Ω–Ω—ñ –∫–Ω–æ–ø–∫–∏
    // console.dir(event.target.elements); //—Ç–∞–∫ –º–æ–∂–Ω–∞ –æ—Ç—Ä–∏–º–∞—Ç–∏ –¥–æ—Å—Ç—É–ø –¥–æ –µ–ª–µ–º–µ–Ω—Ç—ñ–≤ —Ñ–æ—Ä–º–∏
    const formRef = event.target;// —Ç—É—Ç –º—ñ—Å—Ç–∏—Ç—å—Å—è –ø–æ—Å–∏–ª–∞–Ω–Ω—è –Ω–∞ —Ñ–æ—Ä–º—É
    const formData = new FormData(formRef);//—Å—Ç–≤–æ—Ä—é—î–º–æ –Ω–æ–≤–∏–π –æ–±'—î–∫—Ç
    const submittedData = {};//–æ–±'—î–∫—Ç –¥–ª—è –∑–±–æ—Ä—É –¥–∞–Ω–∏—Ö –∑ —Ñ–æ—Ä–º–∏, —è–∫–∏–π –Ω–∞–¥—ñ—à–ª–µ—Ç—å—Å—è –Ω–∞ –±–µ–∫–µ–Ω–¥

    formData.forEach((value, key) => {//—Ü–µ–π –æ–±'—î–∫—Ç –ø—Ä–æ—Å—Ç–æ –º–∞—î —Ñ-—Ü—ñ—é —Ñ–æ—Ä—ñ—á —ñ –¥–∞–Ω—ñ —ñ–Ω–ø—É—Ç—ñ–≤ —É –≤–∏–≥–ª—è–¥—ñ value —Ç–∞ key = name —ñ–Ω–ø—É—Ç–∞
      submittedData[key] = value;//–∑–∞–ø–∏—Å—É—î–º–æ –¥–∞–Ω—ñ –≤ –æ–±'—î–∫—Ç
    });
    console.dir(submittedData);
  };

  if(formModalRef){
    formModalRef.addEventListener('submit', takeFormData);
  };
